/*Styling the footer*/


.footer-container{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0px;
     

}

.footer-container h2{
    font-family: sans-serif;
    font-weight: bold;
    font-size: 20px;
    margin: 0 auto;
    text-align: center;
    text-transform: capitalize;
    
}


.social-links{
    padding: 2px;
    margin: 20px auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    
}

.social-links .social{
    height: 75px;
    width: 100px;
    background-color:transparent;
    margin: 0 5px;
    padding: 2px;
    backdrop-filter: blur;
    cursor: pointer;
    transition: .2s ease;
}

