/*Styling for the header*/

/*Creating the fade in animation of the text and location of the text*/
.header-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 20px;
    transform: translateY(-100%);
    opacity: 0;
    backdrop-filter: blur(20px);
    animation: fade-in 1s ease 1 forwards;
    cursor: pointer;
}

@keyframes fade-in{
    0%{
    transform: translateY(0);
    opacity: 1;
    }
}

.header-container h3{
    display: flex;
    flex-direction: column;
    font-size: 40px;
    text-transform: uppercase;
    font-weight: bold;
}


.header-container h2{
    display: flex;
    flex-direction: column;
    font-size: 30px;
    text-transform: uppercase;
    font-style: italic;
    color:var(--color-pri);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    letter-spacing: 3px;
}

.header-container p span{
    color: rgb(152, 87, 87);
}